import { PresentationLoan } from '@backbase/data-ang/loans';

/**
 * Check for loan overdue
 *
 * @param isOverdue
 * @param inArrearsAmount
 * @param inArrearsDate
 */
export function isLoanOverdue<T extends PresentationLoan = PresentationLoan>({
  isOverdue,
  inArrearsAmount,
  inArrearsDate,
  overduePaymentsCount,
}: T): boolean {
  return Boolean(isOverdue || inArrearsAmount || inArrearsDate || overduePaymentsCount);
}

/**
 * Return the alias for loan account
 *
 * @param alias
 * @param name
 */
export function getLoanAlias<T extends PresentationLoan = PresentationLoan>({ alias, name }: T): string | undefined {
  return alias || alias === '' ? alias : name;
}
